/* src/components/HomePage.css */
.button-container {
    display: flex;
    justify-content: center;
    margin-top: 35px;
    
  }

  .logo {
    width: 300px; 
    height: 300px; 
  }

  .home-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
    height: 100vh; 
  }

  .main-title {
    font-size: 48px;
    margin-bottom: 20px;
  }

  @media (max-width: 780px) {
    .main-title {
      font-size: 32px;
    }
  }
  
  
  
  .start-button,
  .how-to-play-button {
    transition: transform 0.2s;
    background-color: #fff;
    color: #B19EB2;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;
    font-size: 18px;
    font-family: 'Pacifico', cursive;
  }
  
  .start-button:hover,
  .how-to-play-button:hover {
    background-color: #ade9f7;
    transform: scale(1.1); /* Scale up by 10% on hover */
  }
  